/* src/App.css */
body, html, #root, .App {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
}

.content {
  text-align: center;
}


.photo {
  max-width: 80vw;
  max-height: 80vh;
  /* border-radius: 8px; */
  box-shadow: 0 4px 20px rgba(0,0,0,0.1);
  margin-bottom: 1rem;
}

.top-left-name {
  position: absolute;
  top: 1rem;
  left: 1rem;
  font-size: 1.25rem;
  font-weight: bold;
  color: #333;
  background: #f9f9f9;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  z-index: 10;
  pointer-events: none; /* makes it non-clickable */
}

.top-left-name a {
  color: inherit;
  text-decoration: none;
  pointer-events: auto;
}

@media (max-width: 600px) {
  .top-left-name {
    font-size: 1rem;
    top: 0.5rem;
    right: 0.5rem;
  }
}